.all_orders{
    width: 80%;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    border: 1px solid #002E34;
    border-radius: 5px;
    box-shadow: 0 0 10px #002E34;
    background-color: #ffffff;
    color: #002E34;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 500;
    text-align: center;
    letter-spacing: 1px;
    line-height: 1.5;
    z-index: 1;

    .single_order {
        position: relative;
        width: 45%;
        min-width: 340px;
        display: flex; 
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        margin: 10px;
        padding: 10px;
        border: 1px solid #002E34;
        border-radius: 5px;

        p{
            width: 100%;
            display: flex;
            margin: 5px 0;
            b {
                width: 100px;
                display: flex;
                justify-content: flex-start;
            }
        }

        input{
            width: 250px;
            height: 30px;
        }
      
        .order_status {
          width: 140px;
          height: 30px;
          border-radius: 5px;
          margin-bottom: 10px;
          border: none;
          background-color: #d0d703;
          font-size: 20px;
          font-weight: 700;
        }
      
        .okButton {
            position: absolute;
            top: 10px;
            right: 100px;
            height: 35px;
            width: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: #09b231;
            font-size: 20px;
            font-weight: 700;
            border: none;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
        
            &:hover {
              background-color: #ffffff;
              color: #002E34;
              border: 1px solid #002E34;
            }
        
            &:active {
              background-color: #002E34;
              color: #ffffff;
              border: 1px solid #ffffff;
            }
        }

        .delete_btn{
            position: absolute;
            top: 10px;
            right: 10px;
            height: 35px;
            width: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: #ff0000;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
        
            &:hover {
                background-color: #ffffff;
                color: #002E34;
                border: 1px solid #002E34;
            }
        
            &:active {
                background-color: #002E34;
                color: #ffffff;
                border: 1px solid #ffffff;
            }
        
        }

        .edit_btn{
            position: absolute;
            top: 10px;
            right: 55px;
            height: 35px;
            width: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: #fbff00;
            border: none;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
        
            &:hover {
                background-color: #ffffff;
                color: #002E34;
                border: 1px solid #002E34;
            }
        
            &:active {
                background-color: #002E34;
                color: #ffffff;
                border: 1px solid #ffffff;
            }
        }
      }
      
}

@media only screen and (max-width: 905px) {
    .all_orders{
        width: 95%;
    }
}

@media only screen and (max-width: 766px) {
    .all_orders{
        .single_order {
            width: 95%;
            min-width: 320px;
            margin: 5px;
            padding: 5px;
        }
    }
}

@media only screen and (max-width: 375px) {
    .all_orders{
        .single_order {
            font-size: 16px;
            min-width: 280px;

            .order_status {
                width: 120px;
                height: 25px;
                font-size: 16px;
            }

            button{
                width: 90px;
                height: 25px;
                font-size: 16px;
            }
        }
    }
}